@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#background {
  position: fixed;
  height: 100vh;
  width: 100vw;
  background-image: url(/static/media/starrynight.5d689918.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 0;
}

.App {
  display: grid;
}

.lucas-content {
  z-index: 1;
}

.lucas-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
}

.center-column {
  width: 95.5%;
}

.column-half {
  width: 50%;
}

@media only screen and (min-width: 1240px) {
  .center-column {
    width: 1170px;
  } 
}
.BigText {
    height: 97vh;
}

.BigText h3 {
    font-size: 16vh;
    font-family: 'Raleway', sans-serif;
    font-weight: 200;
    text-align: center;
    color: white;
}

.Introductions {
    width: 1170px;
    height: 100vh;
    display: flex;
    flex-direction: row;
}

.Half {
    width: 50%;
    height: 100%;
}

.Card {
    margin: 3% 3% 3% 0%;
}

#Image {
    height: 97%;
}

#PersonalPortrait {
    object-fit: cover;
    width: 100%;
    height: 92vh;
    border-radius: 0.25rem;
}

#Intro {
    height: 75%;
}

#ButtonContainer {
    height: 22%;
    width: 100%;
    margin: 5% 3% 3% 0%;
    display: flex;
    align-content: center;
    justify-content: space-evenly;
}

.NavButton {
    height: 60%;
    width: 30%;
}



@media only screen and (max-width: 1240px) {
    .BigText {
        display: flex;
        justify-content: center;
    }

    .BigText h3 {
        font-size: 10vw;
    }

    .Introductions {
        width: 95%;
    }

    .NavButton {
        font-size: small;
    }
}
img {
    object-fit: contain;
    width: 100%;
    border-radius: 0.25rem;
}
.timeline-button-container {
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-around;
}

.timeline-button-container .MuiFab-root {
    font-size: 1.5rem;
    width: 45px;
    height: 45px;
    box-shadow: none;
}

@media only screen and (min-width: 1240px) {
    .timeline-button-container {
        flex-direction: row;
        width: 45%;
        position: absolute;
        bottom: 0px;
    }
}
.timeline-date {
    color: gray;
}

.timeline-text {
    color: gray;
    padding-left: 3%;
}

.timeline-text h3 {
    color: rgb(69, 69, 69);
}

.SnapImg {
    object-fit: contain;
    width: 100%;
}

@media only screen and (min-width: 1240px) {
    .timeline-body {
        display: flex;
        flex-direction: row;
    }

    .timeline-half {
        width: 50%;
    }
}

.MuiFormControlLabel-root{
    padding-top: 12px;
    width: 60px;
}
.timeline-page {
    padding-top: 3%;
}

@media only screen and (min-width: 1240px) {
    .timeline-page {
        padding-top: 1%;
    }
}
